<template>
  <div class="home">
    <div class="question1">
      <h1>三个运动参数R、φ及Z的关系</h1>
      <p>
        参数解释：R = n<sub>max</sub> /n<sub>min</sub> , Z :变速级数 ,
        φ:标准公比
      </p>
      <p>标准公比φ有：1.06，1.12，1.26，1.41，1.58，1.78，2.0七个数值。</p>
      <h3>
        选择已知参数:
        <select
          @change="reset"
          v-model="mode"
          style="width:100px;padding-bottom:5px"
        >
          <option value="1">已知R、φ</option>
          <option value="2">已知R、Z</option>
          <option value="3">已知φ、Z</option>
        </select>
      </h3>
      <div class="flex">
        <div v-if="mode != 3">
          <div>
            <label style="cursor: pointer;" for="1">输入R</label>
            <input
              id="1"
              name="mod"
              type="radio"
              value="0"
              v-model="moddd"
              key="0"
            />
            <div style="display:inline-block;width:40px"></div>
            <label style="cursor: pointer;" for="2"
              >输入n<sub>max</sub>/n<sub>max</sub></label
            >
            <input
              id="2"
              name="mod"
              type="radio"
              value="1"
              v-model="moddd"
              key="1"
            />
          </div>
          <div v-if="moddd == 0">
            <label for="R">R </label>
            <input id="R" type="number" v-model="R" />
          </div>

          <div v-else>
            <div>
              <label for="R">n<sub>max</sub> </label>
              <input id="R" type="number" @change="change" v-model="nmax" />
            </div>
            <div>
              <label for="R">n<sub>min</sub> </label>
              <input id="R" type="number" @change="change" v-model="nmin" />
            </div>
          </div>
        </div>

        <div v-if="mode != 2">
          <label for="φ">φ </label>
          <input id="φ" type="number" v-model="φ" />
        </div>
        <div v-if="mode != 1">
          <label for="Z">Z </label>
          <input id="Z" type="number" v-model="Z" />
        </div>
        <button @click="mod(mode)">开始计算</button>
      </div>

      <div v-if="mode == 3 && R > 0">
        <h2>输出</h2>
        R的值为：{{ R }}
      </div>
      <div v-if="mode == 2 && φ > 0">
        <h2>输出</h2>
        φ的值为：{{ φ }}
      </div>
      <div v-if="mode == 1 && Z > 0">
        <h2>输出</h2>
        Z的值为：{{ Z }}
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Home",
  data() {
    return { R: 0, φ: 0, Z: 0, nmax: 0, nmin: 0, mode: 1, moddd: 0 };
  },
  methods: {
    change() {
      this.R = (parseFloat(this.nmax) / parseFloat(this.nmin)).toFixed(2);
    },
    mod(num) {
      if (num == 1) {
        if (parseFloat(this.φ) <= 0 || parseFloat(this.R) <= 0) {
          return;
        }
        this.Z = Math.round(
          Math.log10(parseFloat(this.R)) / Math.log10(parseFloat(this.φ)) + 1
        ).toFixed(0);
      }
      if (num == 2) {
        if (parseFloat(this.R) <= 0 || parseFloat(this.Z) <= 0) {
          return;
        }
        this.φ = Math.pow(
          parseFloat(this.R),
          1 / (parseFloat(this.Z) - 1)
        ).toFixed(2);
      }
      if (num == 3) {
        if (parseFloat(this.φ) <= 0 || parseFloat(this.Z) <= 0) {
          return;
        }
        this.R = Math.pow(parseFloat(this.φ), parseFloat(this.Z) - 1).toFixed(
          2
        );
      }
    },
    reset() {
      this.R = 0;
      this.φ = 0;
      this.Z = 0;
      this.nmax = 0;
      this.nmin = 0;
      this.moddd = 0;
    },
  },
};
</script>
