<template>
  <div class="home">
    <div class="question1">
      <h1>结构式的拟定</h1>
      <div>
        <label for="">变速级数：</label>
        <input type="number" @change="change" v-model="Z" />
      </div>

      <button @click="jojo">计算</button>

      <h2 v-if="arr3">
        结构式:{{ Z }}=<span v-for="(a, i) in arr3" :key="a"
          >{{ a }} <sub>{{ arr4[i] }}</sub>
          <span v-if="i < len - 1"> × </span>
        </span>
        <h4>
          <pre>
式中:
  {{ Z }}代表转速级数，
   {{ arr3.join(",") }}表示变速组的传动副数和传动顺序，
  下标{{ arr4.join(",") }}表示各对应变速组的级比指数。
      </pre
          >
        </h4>
      </h2>
    </div>
  </div>
</template>
<script>
export default {
  name: "Home",
  data() {
    return { Z: 0, arr: [], arr2: [], arr3: 0, arr4: [] };
  },
  methods: {
    change() {
      this.arr3 = 0;
    },
    check() {
      this.arr2 = this.arr.map((v) => {
        let count = 0;
        for (let i = 0; i < v.length; i++) {
          if (v[i] == 2 || v[i] == 3) {
            count++;
          }
        }
        return count;
      });

      let num = this.arr2.indexOf(Math.max(...this.arr2));
      this.arr3 = this.arr[num].sort().reverse();
      let arr = [1];
      for (let i = 0; i < this.arr3.length - 1; i++) {
        arr.push(this.arr3[i] * arr[i]);
      }
      this.arr4 = arr;
    },
    jojo() {
      let Z = parseInt(this.Z);
      this.arr = [];
      for (let i = 2; i < Z; i++) {
        for (let j = i; j < Z; j++) {
          if (i * j == Z) {
            this.arr.push([i, j]);
          }
        }
      }

      for (let i = 2; i < Z; i++) {
        for (let j = i; j < Z; j++) {
          for (let m = j; m < Z; m++) {
            if (i * j * m == Z) {
              this.arr.push([i, j, m]);
            }
          }
        }
      }

      for (let i = 2; i < Z; i++) {
        for (let j = i; j < Z; j++) {
          for (let m = j; m < Z; m++) {
            for (let n = m; n < Z; n++) {
              if (i * j * m * n == Z) {
                this.arr.push([i, j, m, n]);
              }
            }
          }
        }
      }

      for (let i = 2; i < Z; i++) {
        for (let j = i; j < Z; j++) {
          for (let m = j; m < Z; m++) {
            for (let n = m; n < Z; n++) {
              for (let n1 = n; n1 < Z; n1++) {
                if (i * j * m * n * n1 == Z) {
                  this.arr.push([i, j, m, n, n1]);
                }
              }
            }
          }
        }
      }

      this.check();
    },
  },
  computed: {
    len() {
      return this.arr3.length;
    },
  },
};
</script>
